<template>
  <div>
    <div class="home-title">
      <span>设备管理</span>
    </div>
    <div class="table-content">
      <div>
        <div class="device-list">
          <div class="device-item" style="text-align: left;">
            <p>
              <span class="device-num" style="color: #1067EE;"> {{deviceCount.TOTAL}}</span>
              <span class="device-unit">台</span>
            </p>
            <p class="devive-numtitle">当前设备总数</p>
          </div>
          <a-divider type="vertical" style="height: 47px;margin-top: 20px;"/>
          <div class="device-item">
            <p>
              <span class="device-num" style="color: #44D7B6;"> {{deviceCount.TOTAL - deviceCount.INACTIVE}}</span>
              <span class="device-unit">台</span>
            </p>
            <p class="devive-numtitle">激活设备数</p>
          </div>
          <a-divider type="vertical" style="height: 47px;margin-top: 20px;"/>
          <div class="device-item">
            <p>
              <span class="device-num"> {{deviceCount.ONLINE}}</span>
              <span class="device-unit">台</span>
            </p>
            <p class="devive-numtitle">在线设备</p>
          </div>
        </div>
      </div>
      <div class="table-title">
        <a-button type="primary" @click="showModal"> <a-icon type="plus"/> 注册设备</a-button>
        <a-select
          style="width: 200px; margin: 0 20px"
          v-model="searchObj.deviceStatus"
          placeholder="请选择设备状态"
          :allowClear="true"
          @search="onSearch"
          @change="onSearch"
        >
          <a-select-option v-for="(item, index) in stateArr" :value="item.value" :key="index">
            {{item.label}}
          </a-select-option>
        </a-select>
        <div>
          <a-select v-model="searchObj.key" style="width: 100px">
            <a-select-option value="deviceLabel">
              设备名称
            </a-select-option>
            <a-select-option value="deviceCode">
              设备标识码
            </a-select-option>
            <a-select-option value="id">
              设备ID
            </a-select-option>
          </a-select>
          <a-input-search v-model="searchObj.val" allowClear placeholder="请搜索" style="width: 200px" @search="onSearch" />
        </div>
      </div>
      <div class="table-box">
        <a-table :columns="columns"
                 @change="tableChange"
                 :rowKey="(record, index) => {return index}"
                 :pagination="pagination"
                 :data-source="data">
          <span slot="deviceStatus" slot-scope="text">
            <span v-for="(item, index) in stateArr" :key="index" v-if="item.value === text">
              <span class="device-status" :style="{background: item.color}"></span>
              {{item.label}}
            </span>
          </span>
          <span slot="nodeType" slot-scope="text">
            <span v-for="(item, index) in nodeArr" :key="index" v-if="item.value === text">
              {{item.label}}
            </span>
          </span>
          <span slot="action" slot-scope="text, record">
        <span class="table-btn" @click="goDetail(record.id, record.productId)">查看</span>
        <a-popconfirm
          ok-text="确定"
          cancel-text="取消"
          @confirm="deleteCancel(record.id)"
        >
           <a-icon slot="icon" type="question-circle-o" style="display: none" />
          <template slot="title">
         <a-alert
           message="您确认删除此设备吗？请注意："
           type="warning"
           show-icon
         >
            <div slot="description">
              <p>1、操作不可撤回：删除后此设备的数据及关联资源将同步删除；</p>
              <p>2、删除后，对应的真实设备需重新关联平台其他设备，才能再次接入平台</p>
            </div>
         </a-alert>
        </template>
         <span class="table-btn"> 删除 </span>
       </a-popconfirm>
        <span class="table-btn" @click="goDebug(record)">调试</span>
        <span class="table-btn" @click="changeStatus(record)">
          <span v-if="record.deviceStatus !== 'STOP'">
            冻结
          </span>
          <span v-else>
            启动
          </span>
        </span>
    </span>
        </a-table>
      </div>
    </div>
    <a-modal v-model="visible" :width="700" title="单设备注册" @ok="handleOk" @cancel="closeHandle">
      <a-form-model
        ref="devForm"
        :model="devform"
        :rules="rules"
        :label-col="labelCol"
        :wrapper-col="wrapperCol"
      >
        <a-form-model-item label="所属产品" prop="productId">
          <a-select
            v-model="devform.productId"
            show-search
            placeholder="请选择或搜索产品"
            option-filter-prop="children"
            @search="productSearch"
          >
            <a-select-option v-for="(item, index) in productArr" :value="item.id" :key="index">
              {{item.productLabel}}
            </a-select-option>
          </a-select>
        </a-form-model-item>
        <a-form-model-item ref="deviceCode" label="设备标识码" prop="deviceCode">
          <a-input v-model="devform.deviceCode" placeholder="请输入设备标识码"/>
        </a-form-model-item>
        <a-form-model-item ref="deviceLabel" label="设备名称">
          <a-input v-model="devform.deviceLabel" placeholder="请输入设备名称"/>
        </a-form-model-item>
        <a-form-model-item ref="authType" label="设备认证类型">
          <a-radio-group v-model="devform.authType">
            <a-radio value="SECRET" checked>
              密钥
            </a-radio>
          </a-radio-group>
        </a-form-model-item>
        <a-form-model-item label="密钥">
          <a-input-password v-model="devform.deviceSecret" placeholder="请输入密钥" />
        </a-form-model-item>
        <a-form-model-item label="确认密钥">
          <a-input-password v-model="devform.confirmSecret" placeholder="请再次输入密钥" />
        </a-form-model-item>
      </a-form-model>
    </a-modal>
  </div>
</template>

<script>
const columns = [
  {
    dataIndex: 'deviceStatus',
    title: '状态',
    ellipsis: true,
    key: 'deviceStatus',
    scopedSlots: { customRender: 'deviceStatus' }
  },
  {
    dataIndex: 'deviceLabel',
    title: '设备名称',
    ellipsis: true,
    key: 'deviceLabel'
  },
  {
    title: '设备标识码',
    dataIndex: 'deviceCode',
    ellipsis: true,
    key: 'deviceCode'
  },
  {
    title: '设备ID',
    dataIndex: 'id',
    ellipsis: true,
    key: 'id'
  },
  {
    title: '节点类型',
    dataIndex: 'nodeType',
    ellipsis: true,
    key: 'nodeType',
    scopedSlots: { customRender: 'nodeType' }
  },
  {
    title: '所属产品',
    key: 'productLabel',
    ellipsis: true,
    dataIndex: 'productLabel'
  },
  {
    title: '操作',
    key: 'action',
    scopedSlots: { customRender: 'action' }
  }
]

export default {
  name: 'equipmentList',
  data () {
    return {
      data: [],
      columns,
      searchObj: {
        key: 'deviceLabel',
        value: '',
        deviceStatus: undefined
      },
      deviceCount: {
        TOTAL: 0,
        INACTIVE: 0,
        ONLINE: 0
      },
      stateArr: [
        {
          value: 'ONLINE',
          color: '#15C937',
          label: '在线'
        },
        {
          value: 'WARN',
          color: '#C91521',
          label: '告警'
        },
        {
          value: 'INACTIVE',
          color: '#beccfa',
          label: '未激活'
        },
        {
          value: 'OFFLINE',
          color: '#C8421B',
          label: '离线'
        },
        {
          value: 'STOP',
          color: '#C8A01B',
          label: 'STOP'
        }
      ],
      nodeArr: [
        {
          value: 'GATEWAY',
          label: '直连设备'
        },
        {
          value: 'ENDPOINT',
          label: '网关子设备'
        }
      ],
      productArr: [],
      pagination: {
        showLessItems: true,
        pageSizeOptions: ['10', '20', '30'],
        showSizeChanger: true,
        pageSize: 10,
        current: 1,
        hideOnSinglePage: false,
        total: 0
      },
      visible: false,
      labelCol: {span: 4},
      wrapperCol: {span: 20},
      other: '',
      devform: {
        deviceLabel: '',
        confirmSecret: '',
        deviceCode: '',
        deviceSecret: '',
        productId: '',
        authType: 'SECRET'
      },
      rules: {
        productId: [
          { required: true, message: '请选择或搜素产品', trigger: 'blur' }
        ],
        deviceCode: [
          { required: true, message: '请输入设备标识码', trigger: 'change' },
          { validator: this.$validator.checkCode, message: '不可输入中文', trigger: ['blur', 'change'] }
        ],
        deviceSecret: [
          { validator: this.$validator.checkCode, message: '不可输入中文', trigger: ['blur', 'change'] }
        ],
        confirmSecret: [
          { validator: this.$validator.checkCode, message: '不可输入中文', trigger: ['blur', 'change'] }
        ]
      }
    }
  },
  created() {
    this.getList()
    this.getDeviceNumber()
  },
  methods: {
    /**
     * 切换分页
     * */
    tableChange(pagination) {
      this.pagination.pageSize = pagination.pageSize
      this.pagination.current = pagination.current
      this.pagination.total = pagination.total
      this.getList()
    },
    /**
     * 获取设备列表
     * */
    getList() {
      const pageOption = {
        searchKey: this.searchObj.key,
        searchValue: this.searchObj.val,
        deviceStatus: this.searchObj.deviceStatus,
        page: this.pagination.current,
        pageSize: this.pagination.pageSize
      }
      this.$api.getDeviceLis(pageOption).then(res => {
        const resData = res.data
        console.log('设备数据', res)
        this.pagination.total = resData.total
        this.pagination.pageSize = resData.size
        this.pagination.current = resData.current
        const resRecords = resData.records
        this.data = resRecords
      })
    },
    /**
     * 获取设备总数
     * */
    getDeviceNumber() {
      const obj = {
        productId: ''
      }
      this.$api.getCountDevice(obj).then(res => {
        console.log('设备数量', res)
        this.deviceCount = res.data
      })
    },
    /**
     * 产品列表
     * */
    getProductList() {
      const obj = {
        limit: '',
        productLabel: ''
      }
      this.$api.getProductAllList(obj).then(res => {
        console.log('产品列表', res)
        this.productArr = res.data
      })
    },
    productSearch(val) {
      const obj = {
        limit: '',
        productLabel: val
      }
      this.$api.getProductAllList(obj).then(res => {
        console.log('搜索的产品', res)
        this.productArr = res.data
      })
    },
    showModal() {
      this.visible = true
      this.devform = {
        authType: 'SECRET'
      }
      this.getProductList()
    },
    handleOk() {
      this.$refs.devForm.validate(valid => {
        if (valid) {
          this.$api.postDevice(this.devform).then(res => {
            this.visible = false
            this.getList()
          })
        }
      })
    },
    closeHandle() {
      this.visible = false
      this.$refs['devForm'].resetFields()
    },
    deleteCancel(id) {
      this.$api.deleteDevice(id).then(res => {
        this.getList()
      })
    },
    onSearch() {
      this.getList()
    },
    goDetail (id, productId) {
      this.$router.push({
        path: '/equipmentDetails',
        query: {
          id: id,
          productId: productId
        }
      })
    },
    goDebug(item) {
      console.log('我传数没有', item)
      let data = JSON.stringify(item)
      this.$router.push({
        path: '/online',
        query: {
          deviceObj: data
        }
      })
    },
    changeStatus(val) {
      const obj = {
        deviceId: val.id,
        deviceStatus: val.deviceStatus
      }
      this.$api.putDeviceStatus(obj).then(res => {
        this.getList()
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.product-info {
  background: #ffffff;
  margin: 24px;
  .product-content {
    padding: 24px;
    color: #8a8e99;
  }
}
.device-list {
  display: flex;
  padding: 24px 24px 0 24px;
  width: 600px;
  .device-item {
    width: 30%;
    text-align: center;
    p {
      margin-bottom: 10px;
    }
    .device-num {
      font-weight: bold;
      font-size: 32px;
      color: #FA6400;
      text-align: left;
    }
    .device-unit {
      color: #999999;
      font-size: 16px;
      font-weight: 400;
      padding-left: 20px;
    }
    .devive-numtitle {
      color: #6666;
      font-weight: 400;
      font-size: 14px;
    }
  }
}
</style>



